import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import * as p5 from "p5";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController,
    private alert: AlertController) {
    this.start_alert();
  }

  start_alert() {
    this.alert.create({
      title: '',
      message: '캔버스 사이즈를 입력해주세요',
      enableBackdropDismiss: false,
      inputs: [{
        name: 'size',
        type: 'number',
        placeholder: '기본값: 512'
      },
      {
        name: 'height',
        type: 'number',
        placeholder: '기본값: 정사각형'
      }],
      buttons: [{
        text: '확인',
        handler: (ev) => {
          this.run_canvas_with(ev['size'] || 512, ev['height'] || 0);
        }
      }]
    }).present();
  }

  /**
   * 캔버스 레이어 생성
   * @param layer_size 레이어 한 변의 길이
   */
  run_canvas_with(layer_size: number = 512, layer_height?: number) {
    let sketch = (p5: p5) => {
      /** 현재 어떤 행동중인가 */
      enum ACTION_STATUS {
        IDLE, PLAY_ANIMATION, CHANGE_VALUE,
        ON_DRAW, CHANGE_TIMELINE, CHANGE_LAYER,
        CHANGE_BRUSH, IS_INDICATING,
        CHANGE_CURRENT_LINE_COLOR,
      }
      var status: ACTION_STATUS = ACTION_STATUS.IDLE;
      var brushes: BrushDeck[] = [];
      var selected_brush: number = 0;
      /** background layer */
      var pg: p5.Graphics;
      /** 작업중인 개체를 레이어로 떼어낸 것, 다중연산 최적화용 */
      var tmp_work_layer: p5.Graphics;
      // indicator_value below
      var start_pos: p5.Vector = p5.createVector(0, 0);
      var current_pos: p5.Vector = p5.createVector(0, 0); // with shift+LB // reset with MB
      var target_pos: p5.Vector = p5.createVector(0, 0);
      var start_scale: number = 1;
      var current_scale: number = 1; // with wheel-UP/DOWN // reset with MB
      var target_scale: number = 1;
      const MENU_FONT_SIZE = 16;
      const IONIC_HEIGHT_MARGIN = 72;
      /** 색상 */
      var next_draw_color: p5.Color;
      var current_drawline_picker = p5.createColorPicker(next_draw_color);
      /** 슬라이더 값들을 오브젝트로 통합  
       * parameter ref 개념이 js에 없음..
       */
      var slider_values = {
        /** 그리기 선의 크기 */
        next_draw_size: 20,
        /** 위치 흩뿌리기 */
        scatter_position_value: 0,
        /** 꼬리비율값 */
        scatter_tail_value: 0,
        /** 크기 흩뿌리기 */
        scatter_size_value: 0,
        randomize_rotation: 1,
        draw_steps: 2,
        brush_opacity: 255,
      }
      /** 붓 크기 */
      var current_brush_size_slider: p5.Element = p5.createSlider(1, layer_size / 4, slider_values.next_draw_size);
      /** 랜덤 흩뿌리기 */
      var scatter_position_slider: p5.Element = p5.createSlider(0, layer_size / 8, slider_values.scatter_position_value);
      /** 랜덤 위치 흩뿌리기 꼬리 비율 조정 */
      var scatter_tail_slider: p5.Element = p5.createSlider(0, 1, slider_values.scatter_tail_value, .035);
      /** 랜덤 브러쉬 사이즈 */
      var scatter_size_slider: p5.Element = p5.createSlider(-1, 1, slider_values.scatter_size_value, .07);
      /** 붓의 회전 랜덤 정도 조정 */
      var randomize_rotate_brush_slider: p5.Element = p5.createSlider(0, 1, slider_values.randomize_rotation, .035);
      /** 붓의 밀도 (steps) */
      var current_brush_steps: p5.Element = p5.createSlider(1, 24, slider_values.draw_steps);
      /** 붓의 투명도 */
      var current_brush_opacity: p5.Element = p5.createSlider(0, 255, slider_values.brush_opacity);
      p5.setup = () => {
        brushes.push(new BrushDeck('assets/brushes/basic_brush.png', '기본 붓(Default)'));
        // *** 파일을 올려서 브러쉬 추가하기
        // ...
        const tmp = document.getElementById('p5panel');
        let panel = p5.createCanvas(tmp.clientWidth, tmp.clientHeight);
        panel.parent('p5panel');

        pg = p5.createGraphics(layer_size, (layer_height || layer_size));
        pg.imageMode(p5.CENTER);
        pg.smooth();
        pg.noLoop();

        tmp_pg = p5.createGraphics(layer_size, (layer_height || layer_size));
        tmp_pg.imageMode(p5.CENTER);
        tmp_pg.smooth();
        tmp_pg.noLoop();

        p5.imageMode(p5.CENTER);
        add_layer();
        p5.smooth();
        p5.frameRate(60);
        p5.noLoop();
        p5.textSize(18);
        // 아래, 설정 메뉴 DOM
        next_draw_color = p5.color(current_drawline_picker.value() as string);
        // 그리기 색상
        current_drawline_picker.remove();
        current_drawline_picker = p5.createColorPicker(next_draw_color);
        current_drawline_picker.mouseClicked(() => {
          status = ACTION_STATUS.CHANGE_CURRENT_LINE_COLOR;
          p5.loop();
        });
        current_drawline_picker.position(16, IONIC_HEIGHT_MARGIN + 24);
        current_drawline_picker.size(60, 60);
        // 그리기 붓 크기
        realtime_slider_form(current_brush_size_slider, IONIC_HEIGHT_MARGIN + 120, 'next_draw_size', 'size');
        // 위치 흩뿌리기
        realtime_slider_form(scatter_position_slider, IONIC_HEIGHT_MARGIN + 170, 'scatter_position_value', 'scatter_value');
        // 랜덤 값들이 붓 끝으로부터 감쇠하는 정도
        realtime_slider_form(scatter_tail_slider, IONIC_HEIGHT_MARGIN + 220, 'scatter_tail_value', 'scatter_reduce_tail');
        // 크기 랜덤화
        realtime_slider_form(scatter_size_slider, IONIC_HEIGHT_MARGIN + 270, 'scatter_size_value', 'scatter_size');
        // 회전 정도
        realtime_slider_form(randomize_rotate_brush_slider, IONIC_HEIGHT_MARGIN + 320, 'randomize_rotation', 'rotate_ratio')
        // 붓의 밀도
        realtime_slider_form(current_brush_steps, IONIC_HEIGHT_MARGIN + 370, 'draw_steps', 'draw_step');
        // 붓의 투명도
        realtime_slider_form(current_brush_opacity, IONIC_HEIGHT_MARGIN + 420, 'brush_opacity', 'brush_opacity');
      }
      /**
       * 실시간 변경처리되는 슬라이더 폼
       * @param target_slider 해당 슬라이더
       * @param pos 슬라이더 높이
       * @param current_value_name 슬라이더 값의 이름
       * @param data_name 레이어 info.draws.~ 에 해당하는 이름
       */
      var realtime_slider_form = (target_slider: p5.Element, pos: number,
        current_value_name: string, data_name: string, horizontal_pos: number = 16) => {
        target_slider.position(horizontal_pos, pos);
        target_slider.style('width', '110px');
        target_slider.mouseMoved(() => {
          if (current_scribble < 0)
            return;
          layer_info[current_layer].draws[data_name][current_scribble] = target_slider.value() as number;
          if (layer_info[current_layer].draws[data_name].length == current_scribble + 1)
            slider_values[current_value_name] = layer_info[current_layer].draws[data_name][current_scribble];
          p5.redraw();
          update_tmp_line();
        });
        target_slider.mouseOut(() => {
          update_current_layers();
        });
      }
      /** 레이어 생성시 사용되는 슬라이더 폼 */
      var layer_info_slider_form = () => {

      }
      p5.draw = () => {
        draw_background();
        lerp_indicator();
        draw_timeline();
        update_all_layers();
        switch (status) { // 사용자 행동에 따른 실시간 처리
          case ACTION_STATUS.CHANGE_CURRENT_LINE_COLOR:
            layer_info[current_layer].draws.color[layer_info[current_layer].draws.color.length - 1] = p5.color(current_drawline_picker.value() as string);
            if (layer_info[current_layer].draws.color.length == current_scribble + 1)
              next_draw_color = p5.color(current_drawline_picker.value() as string);
            update_tmp_line();
            break;
        }
      }
      /** 편집 때 실시간 변화를 보여주는 임시 레이어 */
      var tmp_pg: p5.Graphics;
      /** 편집 중인 선만 업데이트*/
      var update_tmp_line = () => {
        tmp_pg.clear();
        tmp_pg.background(255, 225);
        draw_on_layer_by_info(current_scribble, true)
        p5.image(tmp_pg,
          (p5.width / 2),
          (p5.height / 2)
        );
      }
      /** 편집 레이어만 전부 업데이트 */
      var update_current_layers = () => {
        layers[current_layer].clear();
        for (let i = 0; i < layer_info[current_layer].draws.lines.length; i++)
          draw_on_layer_by_info(i);
      }
      /** 모든 레이어 업데이트 */
      var update_all_layers = () => {
        for (let i = 0, j = layers.length; i < j; i++) {
          layers[i].redraw();
          p5.image(layers[i],
            (p5.width / 2),
            (p5.height / 2)
          );
        }
        p5.redraw();
      }
      /** 브러쉬 추가시 보이는 이미지 */
      class BrushDeck {
        /** 보여지는 샘플 */
        public pg: p5.Graphics;
        /** 브러쉬 그 자체 */
        public img: p5.Image;
        constructor(_data_img: string, _brush_name: string) {
          p5.loadImage(_data_img, v => {
            this.img = v;
            this.pg = p5.createGraphics(v.width, v.height);
            this.pg.smooth();
            this.pg.imageMode(p5.CENTER);
            this.pg.image(this.img, pg.width / 2, pg.height / 2, pg.width, pg.height);
            this.pg.noLoop();
          }, e => {
            last_log = "브러쉬 불러오기 실패: " + e;
          });
        }
        public _is_clicked() {
          if (p5.mouseIsPressed)
            if (p5.mouseButton == p5.LEFT) {
              console.log("브러쉬 선택 코드 필요");
            } else if (p5.mouseButton == p5.RIGHT) {
              console.log("브러쉬 삭제 코드 필요");
            }
        }
      }
      /** 하단 프레임 슬라이더 */
      class FrameSlider {
        /** 보여지는 프레임 현황판 */
        public pg: p5.Graphics;
        // *** 레이어간 간격 조정하기
        // *** 레이어 진행도 표기 (막대)
        // *** 붓의 크기 표기 (기울기)
        // *** 붓의 투명도 표기 (투명도)
        // *** 붓의 scatter(무엇이든) 표기 (일그러짐)
        // *** 애니메이션 재생 버튼
        // *** 일시 정지 및 정지버튼
        constructor() {

        }
        public _is_clicked() {

        }
      }
      var start_lerp: number = 0;
      var current_lerp: number = 0;
      const LERP_SIZE: number = .07;
      /** 네비게이션 화면 변동 */
      var lerp_indicator = () => {
        if (current_scale == target_scale && current_pos == target_pos)
          return;
        p5.image(pg,
          (p5.width / 2),
          (p5.height / 2)
        );
      };
      /** 네비게이션 초기화 */
      var reset_indicator = () => {
        target_pos = p5.createVector(0, 0);
        target_scale = 1;
      }
      /** 타임라인 그리기 */
      var draw_timeline = () => {
      };
      /** 보고있는 현재 프레임 */
      var view_frame: number = 0;
      /** 멈추게 될 프레임 (전체 프레임 수) */
      var final_frame: number = 0;
      /**
       * 실시간 선 그리기
       * @param _lines 그릴 선
       * @param _steps 붓 빈도
       */
      var draw_on_layer = (_lines: p5.Vector[],
        _color: p5.Color,
        _steps: number) => {
        let ll = _lines.length - 1;
        let retargeting_mouse_x: number = (p5.width - layer_size) / 2;
        let retargeting_mouse_y: number = (p5.height - (layer_height || layer_size)) / 2;
        _color.setAlpha(slider_values.brush_opacity);
        for (let i = 1; i <= _steps; i++) {
          let t: number = i / _steps;
          let x = p5.curvePoint(
            ll - 3 > 0 ? _lines[ll - 3].x : _lines[0].x,
            ll - 2 > 0 ? _lines[ll - 2].x : _lines[0].x,
            ll - 1 > 0 ? _lines[ll - 1].x : _lines[0].x,
            _lines[ll].x,
            t
          );
          let y = p5.curvePoint(
            ll - 3 > 0 ? _lines[ll - 3].y : _lines[0].y,
            ll - 2 > 0 ? _lines[ll - 2].y : _lines[0].y,
            ll - 1 > 0 ? _lines[ll - 1].y : _lines[0].y,
            _lines[ll].y,
            t
          );
          layers[current_layer].push();
          layers[current_layer].tint(_color);
          layers[current_layer].translate(x - retargeting_mouse_x
            + p5.random(-slider_values.scatter_position_value, slider_values.scatter_position_value),
            y - retargeting_mouse_y + p5.random(-slider_values.scatter_position_value, slider_values.scatter_position_value));
          layers[current_layer].rotate(p5.random(p5.PI) * slider_values.randomize_rotation);
          layers[current_layer].scale(slider_values.next_draw_size
            + (p5.random(-slider_values.next_draw_size, slider_values.next_draw_size) * slider_values.scatter_size_value));
          layers[current_layer].image(brushes[selected_brush].img,
            0, 0, 1, 1);
          layers[current_layer].pop();
        }
        layers[current_layer].redraw();
        p5.redraw();
      }
      /** 기존 정보로 선 그리기  
       * 임시 레이어 전환 기능 있음
       */
      var draw_on_layer_by_info = (lineNum: number, is_tmp: boolean = false) => {
        let target_layer: p5.Graphics = layers[current_layer];
        if (is_tmp)
          target_layer = tmp_pg;
        let _lines: p5.Vector[] = layer_info[current_layer].draws.lines[lineNum];
        let _color: p5.Color = layer_info[current_layer].draws.color[lineNum];
        let _opacity: number = layer_info[current_layer].draws.brush_opacity[lineNum];
        _color.setAlpha(_opacity);
        let _steps: number = layer_info[current_layer].draws.draw_step[lineNum];
        p5.randomSeed(lineNum);
        for (let ll = 0, b = _lines.length; ll < b; ll++)
          for (let i = 1; i <= _steps; i++) {
            let t: number = i / _steps;
            let x = p5.curvePoint(
              ll - 3 > 0 ? _lines[ll - 3].x : _lines[0].x,
              ll - 2 > 0 ? _lines[ll - 2].x : _lines[0].x,
              ll - 1 > 0 ? _lines[ll - 1].x : _lines[0].x,
              _lines[ll].x,
              t
            );
            let y = p5.curvePoint(
              ll - 3 > 0 ? _lines[ll - 3].y : _lines[0].y,
              ll - 2 > 0 ? _lines[ll - 2].y : _lines[0].y,
              ll - 1 > 0 ? _lines[ll - 1].y : _lines[0].y,
              _lines[ll].y,
              t
            );
            if (layer_info[current_layer].draws.size[lineNum] > 0) { // *** 향후 tails 계산식 추가
              target_layer.push();
              target_layer.tint(_color);
              target_layer.translate(x - layer_info[current_layer].draws.retargetting[lineNum].x
                + p5.random(-layer_info[current_layer].draws.scatter_value[lineNum], layer_info[current_layer].draws.scatter_value[lineNum]) * p5.constrain((ll * _steps + i) / (b * _steps * layer_info[current_layer].draws.scatter_reduce_tail[lineNum]), 0, 1),
                y - layer_info[current_layer].draws.retargetting[lineNum].y
                + p5.random(-layer_info[current_layer].draws.scatter_value[lineNum], layer_info[current_layer].draws.scatter_value[lineNum]) * p5.constrain((ll * _steps + i) / (b * _steps * layer_info[current_layer].draws.scatter_reduce_tail[lineNum]), 0, 1));
              target_layer.rotate(p5.random(p5.PI) * layer_info[current_layer].draws.rotate_ratio[lineNum]);
              target_layer.scale(
                layer_info[current_layer].draws.size[lineNum] + (p5.random(-layer_info[current_layer].draws.size[lineNum], layer_info[current_layer].draws.size[lineNum]) * layer_info[current_layer].draws.scatter_size[lineNum]));
              target_layer.image(brushes[selected_brush].img,
                0, 0, 1, 1);
              target_layer.pop();
            }
          }
        target_layer.redraw();
        p5.redraw();
      }

      interface DrawForm {
        /** 그린 선 / 필터에서는 각 점들의 추가정보 */
        lines?: p5.Vector[][];
        /** 그린 선 색상 */
        color?: p5.Color[];
        /** 그린 선의 브러쉬 크기  
         * 선마다 배열로 정렬됨  
         * 필터값으로 사용시 사이즈 흩뿌리기의 구체적인 상대 수치
         */
        size?: number[];
        /** 위치 재조정값  
         * 레이어 상에서 그리기 포인터의 재조정 위치  
         * 건들이지 말 것  
         * 필터값으로 사용시 흩뿌리기 위치의 구체적인 상대 수치
         */
        retargetting?: p5.Vector[];
        /** 흩뿌리기 정도 */
        scatter_value?: number[];
        /** 랜덤 사이즈 */
        scatter_size?: number[];
        /** 감쇄가 차지하는 비율(끝으로부터) */
        scatter_reduce_tail?: number[];
        /** 선별 붓 회전 정도 */
        rotate_ratio?: number[];
        /** 이 레이어에서 사용할 붓 모양의 번호 */
        selected_brush?: number[],
        /** 이 선에서 사용할 그리기의 빈도 */
        draw_step?: number[];
        /** 브러쉬에 적용될 투명도 */
        brush_opacity?: number[];
        // /** 사각사각 칠하기의 크기(너비) (최대치로부터의 비율) */
        // fill_scribble_value?: number[];
        // /** 사각사각 칠하기의 크기 감쇄 */
        // fill_scribble_reduce?: number[];
        // /** 사각사각 감쇄가 차지하는 비율(끝으로부터) */
        // fill_scribble_tail?: number[];
      }
      interface LayerForm {
        /** 그리기 */
        draws?: DrawForm;
      }
      var new_layer_form = (): LayerForm => {
        let result: LayerForm = {
          draws: {
            lines: [],
            color: [],
            size: [],
            scatter_value: [],
            scatter_size: [],
            scatter_reduce_tail: [],
            rotate_ratio: [],
            selected_brush: [0],
            retargetting: [],
            draw_step: [],
            brush_opacity: [],
          },
        };
        return result;
      }
      var draw_call: number = 0;
      /** 가장 배경부분 새로고침 */
      var draw_background = () => {
        p5.background(200);
        p5.push();
        p5.translate(120, 20);
        p5.rotate(draw_call);
        p5.line(0, 10, 0, -10);
        p5.line(10, 0, -10, 0);
        p5.pop();
        p5.push();
        p5.textSize(MENU_FONT_SIZE);
        p5.text('그리기 색상', 0, MENU_FONT_SIZE);
        p5.text('그리기 크기', 0, 110);
        p5.text('위치 흩뿌리기', 0, 160);
        p5.text('흩뿌리기 꼬리비율', 0, 210);
        p5.text('랜덤 브러쉬 크기', 0, 260);
        p5.text('붓의 회전 정도', 0, 310);
        p5.text('붓의 밀도', 0, 360);
        p5.text('붓의 투명도', 0, 410);
        p5.pop();
        pg.background(255);
        pg.redraw();
        p5.redraw();
        draw_call += .1;
      };
      /** 현재 작업중인 레이어 */
      var current_layer: number = 0;
      /** 현재 작업중인 선 */
      var current_scribble: number = -1;
      /** 레이어들 (이미지: pg) */
      var layers: p5.Graphics[] = [];
      /** 레이어에 저장된 정보들 */
      var layer_info: LayerForm[] = [];
      /** 레이어 생성하기 */
      var add_layer = () => {
        if (layers.length < 255) {
          let n: p5.Graphics = p5.createGraphics(layer_size, (layer_height || layer_size));
          n.imageMode(p5.CENTER);
          n.smooth();
          n.noLoop();
          layers.push(n);
          layer_info.push(new_layer_form());
        } else {
          view_log("최대 활용 레이어 숫자 초과! 더 이상 만드는 것을 허용하지 않습니다.");
        }
      };
      /** 레이어 삭제하기 */
      var remove_layer = (num) => {
        layers.splice(num, 1);
      };
      /** 현재 프레임에 랜덤 필터값 적용 */
      var randomSeed_position_frame = () => {
        console.log("randomSeedFrame");
      }
      /** 현재 레이어에 랜덤 필터값 적용 */
      var randomSeed_position_layer = () => {
        console.log("randomSeedLayer");
      }
      var randomSeed_rotation_frame = () => {
        console.log("randomSeedFrame");
      }
      /** 현재 레이어에 랜덤 필터값 적용 */
      var randomSeed_rotation_layer = () => {
        console.log("randomSeedLayer");
      }
      /** 전체 랜덤 필터 적용 */
      var randomSeed_scene = () => {
        console.log("randomSeedScene");
      }
      /** 미리보기 플레이 */
      var animate_scribbles = () => {
        view_frame = 0;
        reset_indicator();
      }
      /** 미리보기 종료 */
      var cancel_animation_scribbles = () => {
        view_frame = final_frame;
      }
      /** 작업중이던 그리기 내용을 json 파일로 저장하기 */
      var _save_work_json = () => {

      }
      /** 작업중이던 필터 정보를 json 파일로 저장하기 */
      var _save_work_filter = () => {

      }
      /** 작업 json 현 캔버스에 적용 */
      var _load_work_json = () => {

      }
      /** 작업 필터 현 캔버스에 적용 */
      var _load_work_filter = () => {

      }
      /** 화면에 보여지는 마지막 로그 */
      var last_log = "그리기 프로그램 시작함";
      /** 로그 남기기 */
      var view_log = (str: string) => {
        console.log(str);
        last_log = str;
      };
      p5.mousePressed = (ev) => {
        // 향후 indicator 정보와 일치시킬 것
        if (p5.mouseX < p5.width / 2 + layer_size / 2
          && p5.mouseX > p5.width / 2 - layer_size / 2
          && p5.mouseY < p5.height / 2 + (layer_height || layer_size) / 2
          && p5.mouseY > p5.height / 2 - (layer_height || layer_size) / 2) {
          status = ACTION_STATUS.ON_DRAW;
          layer_info[current_layer].draws.lines.push([p5.createVector(p5.mouseX, p5.mouseY)]);
          layer_info[current_layer].draws.color.push(next_draw_color);
          layer_info[current_layer].draws.size.push(slider_values.next_draw_size);
          layer_info[current_layer].draws.scatter_value.push(slider_values.scatter_position_value);
          layer_info[current_layer].draws.scatter_size.push(slider_values.scatter_size_value);
          layer_info[current_layer].draws.scatter_reduce_tail.push(slider_values.scatter_tail_value);
          layer_info[current_layer].draws.rotate_ratio.push(slider_values.randomize_rotation);
          layer_info[current_layer].draws.draw_step.push(slider_values.draw_steps);
          layer_info[current_layer].draws.brush_opacity.push(slider_values.brush_opacity);
          current_scribble++;
          let retargeting_mouse_x: number = (p5.width - layer_size) / 2;
          let retargeting_mouse_y: number = (p5.height - (layer_height || layer_size)) / 2;
          layer_info[current_layer].draws.retargetting.push(p5.createVector(retargeting_mouse_x, retargeting_mouse_y));
          p5.randomSeed(current_scribble);
          draw_on_layer(layer_info[current_layer].draws.lines[layer_info[current_layer].draws.lines.length - 1],
            next_draw_color,
            layer_info[current_layer].draws.draw_step[layer_info[current_layer].draws.draw_step.length - 1]);
        } else {
          status = ACTION_STATUS.CHANGE_VALUE;
          p5.keyPressed();
        }
      }
      p5.mouseDragged = () => {
        switch (status) {
          case ACTION_STATUS.ON_DRAW:
            layer_info[current_layer].draws.lines[layer_info[current_layer].draws.lines.length - 1].push(p5.createVector(p5.mouseX, p5.mouseY));
            draw_on_layer(layer_info[current_layer].draws.lines[layer_info[current_layer].draws.lines.length - 1],
              next_draw_color,
              layer_info[current_layer].draws.draw_step[layer_info[current_layer].draws.draw_step.length - 1]);
            break;
        }
      }
      p5.mouseReleased = () => {
        switch (status) {
          case ACTION_STATUS.ON_DRAW:
            console.log("info: ", layer_info);
            layer_info[current_layer].draws.lines[layer_info[current_layer].draws.lines.length - 1].push(p5.createVector(p5.mouseX, p5.mouseY));
            draw_on_layer(layer_info[current_layer].draws.lines[layer_info[current_layer].draws.lines.length - 1],
              next_draw_color,
              layer_info[current_layer].draws.draw_step[layer_info[current_layer].draws.draw_step.length - 1]);
            status = ACTION_STATUS.IDLE;
            p5.noLoop();
            break;
          case ACTION_STATUS.CHANGE_CURRENT_LINE_COLOR:
            update_current_layers();
            break;
        }
      }
      p5.keyPressed = () => {
        status = ACTION_STATUS.IDLE;
        p5.noLoop();
      }
      p5.windowResized = () => {
        const tmp = document.getElementById("p5panel");
        p5.resizeCanvas(tmp.clientWidth, tmp.clientHeight);
      }
    };

    new p5(sketch);
  }
}



